const body = document.querySelector("body");
const mobileNav = document.querySelector(".mobile-nav");
const menuBtn = document.querySelector(".menu-btn");

menuBtn.addEventListener("click", () => {
  mobileNav.classList.toggle("active");
  if (mobileNav.classList.contains("active")) {
    body.classList.add("scroll-lock");
  } else {
    body.classList.remove("scroll-lock");
  }
});

// ////////////////////////////////////////////
const header = document.querySelector("header");

window.addEventListener("scroll", () => {
  const windowPosition = window.scrollY > 0;
  header.classList.toggle("on-scroll", windowPosition);
});

// ////////////////////////////////////////////
// const section = document.getElementById("stats");
const digits = document.querySelectorAll(".digit");
// console.log(section);

// const scrollPositon = section.offsetHeight;
// if (window.scrollY === scrollPositon) {
// }
digits.forEach((digit) => {
  digit.innerText = `0`;

  function updateNumbers() {
    const target = Number(digit.getAttribute("data-target"));
    const i = Number(digit.innerText);
    const incrementBy = target / 1000;
    if (i < target) {
      digit.innerText = `${Math.ceil(i + incrementBy)}`;
      setTimeout(updateNumbers, 1);
    } else {
      digit.innerText = target;
    }
  }
  updateNumbers();
});

// video wrapper
const videoWrapper = document.querySelector(".video-wrapper");
const videoBtn = document.querySelector(".video-popup");
const closeBtn = videoWrapper.querySelector(".close-btn");
// const video = document.getElementById("video");
// let src = video.getAttribute("src");
// src += `${src}&autoplay=1`;
// console.log(src);

videoBtn.addEventListener("click", () => {
  // video.setAttribute("src", src);
  videoWrapper.classList.toggle("show");
  if (videoWrapper.classList.contains("show")) {
    body.classList.add("scroll-lock");
  }
});
closeBtn.addEventListener("click", () => {
  videoWrapper.classList.remove("show");
  body.classList.remove("scroll-lock");
});
